import { createApp } from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import axios from "axios";
import VueSession from "vue-session";
import Paginate from "vuejs-paginate-next";
import ElementPlus from "element-plus";
import "element-plus/dist/index.css";
import * as ElementPlusIconsVue from "@element-plus/icons-vue";
//import { BootstrapVue, IconsPlugin } from "bootstrap-vue";

// import twLib from "./assets/js/twLib";

// bootstrap framework  Vue  
// boorstrap    css  .
//    bootstrap   .
//    v2.22.0()  Bootstrap v4.6.1, Vue.js v2.6.12 .
//  package.json   Bootstrap 
// bootstrap-vue : v2.21.2
// bootstrap : 3.3.x,
// vue.js : v3.2.31          .
import "bootstrap-vue/dist/bootstrap-vue.css";
//   
import "bootstrap/dist/css/bootstrap.css";

import "./assets/css/admin/reset.css";
import "./assets/css/admin/basic.css";
import "./assets/css/admin/common.css";
import "./assets/css/admin/layout.css";
// import "./assets/css/jh_css/style.css";

// import Event bus support library
import mitt from "mitt";

//    
// vue  session   session  .
//             .
//          .
var sessionOptions = {
  persist: true,
};

//   json         
axios.defaults.headers.post["Content-Type"] =
  "application/x-www-form-urlencoded";

const app = createApp(App);
const emitter = mitt();

// Declare globalProperties
// app   axios   
app.config.globalProperties.axios = axios;
app.config.productionTip = false;
// set globalProperties "emitter" that mitt Object ref-variable
app.config.globalProperties.emitter = emitter;

// store, router, vueSession, sessionOptions  
//     app 

app
  .use(store)
  .use(router)
  .use(VueSession, sessionOptions)
  .use(Paginate)
  //.use(twLib)
  .use(ElementPlus)
  .mount("#app");

for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
  app.component(key, component);
}

