<template>
  <div class="modal" v-if="isVisible">
    <div class="modal-content">
      <span class="close" @click="hideModal">&times;</span>
      <h1></h1>
      <!--   -->
      <form @submit.prevent="registerUser">
        <div class="form-group">
          <label for="loginID">ID</label>
          <input
            type="text"
            id="loginID"
            name="loginID"
            v-model="userForm.loginID"
            required
          />
          <!-- ID    -->
          <p
            v-if="userIdDuplicateCheckMessage"
            :class="{
              'error-message': isUserIdDuplicate,
              'success-message': !isUserIdDuplicate,
            }"
          >
            {{ userIdDuplicateCheckMessage }}
          </p>
        </div>
        <div class="form-group">
          <label for="name"></label>
          <input
            type="text"
            id="name"
            name="name"
            v-model="userForm.name"
            required
          />
          <div class="form-group">
            <label for="email"></label>
            <input
              type="email"
              id="email"
              name="email"
              v-model="userForm.email"
              required
            />
            <!--      -->
            <p
              v-if="emailDuplicateCheckMessage"
              :class="{
                'error-message': isEmailDuplicate,
                'success-message': !isEmailDuplicate,
              }"
            >
              {{ emailDuplicateCheckMessage }}
            </p>
          </div>
        </div>
        <div class="form-group">
          <label for="password"></label>
          <input
            type="password"
            id="password"
            name="password"
            v-model="userForm.password"
            required
          />
        </div>

        <!--    -->
        <div class="form-group">
          <label for="hp"></label>
          <input
            type="text"
            id="hp"
            name="hp"
            v-model="userForm.hp"
            placeholder="'-'  "
            required
            maxlength="11"
          />
          <!--      -->
          <!--     -->
          <button @click.prevent="requestVerificationCode">
            {{ verificationRequested ? "" : "  " }}
          </button>
          <p v-if="hpError" class="error-message">{{ hpError }}</p>
        </div>

        <!--   -->
        <div class="form-group" v-show="false">
          <label for="naver"></label>
          <input v-model="userForm.naver" />
        </div>
        <div class="form-group" v-show="false">
          <label for="google"></label>
          <input v-model="userForm.google" />
        </div>
        <div class="form-group" v-show="false">
          <label for="kakao"></label>
          <input v-model="userForm.kakao" />
        </div>

        <!--    , verificationRequested true  . -->
        <div class="form-group" v-if="verificationRequested">
          <label for="verificationCode"> </label>
          <input
            type="text"
            id="verificationCode"
            name="verificationCode"
            v-model="verificationCode"
            required
            @input="verifyCodeInput"
            maxlength="6"
          />
          <p v-if="codeVerificationMessage" :class="messageClass">
            {{ codeVerificationMessage }}
          </p>
        </div>

        <button type="submit"></button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      isVisible: false,
      verificationRequested: false, //      
      verificationCode: "", //    
      correctVerificationCode: "", //      
      hpError: null, //   
      codeVerificationMessage: "", //     
      messageClass: "",
      verificationCodeTimeout: null, //    ID
      isUserIdDuplicate: false, // 
      userIdDuplicateCheckMessage: "", // 
      emailDuplicateCheckMessage: "", //     
      isEmailDuplicate: false, //   
      emailFormatValid: true, //    
      //      
      userForm: {
        loginID: "",
        name: "",
        email: "",
        password: "",
        hp: "",
        naver: "N",
        google: "N",
        kakao: "N",
      },
      props: {
        name: String,
        ph: String,
        naver: String,
        google: String,
        kakao: String,
      },
    };
  },
  //    
  watch: {
    "userForm.loginID": function (newValue) {
      this.checkUserIdDuplicate(newValue);
    },
    "userForm.email"(newValue) {
      this.checkEmailDuplicate(newValue);
    },
  },
  methods: {
    showModal() {
      this.isVisible = true;
    },
    hideModal() {
      this.isVisible = false;

      //     
      this.userForm = {
        loginID: "",
        name: "",
        password: "",
        hp: "",
        email: "",
        naver: "N",
        google: "N",
        kakao: "N",
      };

      //   
      this.verificationRequested = false;
      this.verificationCode = "";
      this.correctVerificationCode = "";
      this.hpError = null;
      this.codeVerificationMessage = "";
      this.messageClass = "";

      //    
      clearTimeout(this.verificationCodeTimeout); //   
      this.verificationCodeTimeout = null;
      this.timer = null;
    },
    //  
    checkUserIdDuplicate(loginID) {
      console.log("  : ", loginID);
      if (!loginID.trim()) {
        this.userIdDuplicateCheckMessage = "";
        return;
      }
      //   ID   API   
      axios
        .post("/api/checkID", { loginID })

        .then((response) => {
          console.log("  : ", response.data);
          if (response.data === 1) {
            this.isUserIdDuplicate = true;
            this.userIdDuplicateCheckMessage = "   .";
          } else {
            this.isUserIdDuplicate = false;
            this.userIdDuplicateCheckMessage = "  .";
          }
        })
        .catch((error) => {
          console.error("     :", error);
        });
    },
    //   
    validateEmailFormat(email) {
      const pattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      this.emailFormatValid = pattern.test(email);
      return this.emailFormatValid; //     
    },
    //   
    checkEmailDuplicate(email) {
      //       .
      if (!email.trim()) {
        this.isEmailDuplicate = false; //   
        this.emailDuplicateCheckMessage = ""; //  
        return; //  
      }

      //   
      if (!this.validateEmailFormat(email)) {
        this.isEmailDuplicate = true;
        this.emailDuplicateCheckMessage = "   .";
        return; //        .
      }

      //      .
      axios
        .post("/api/checkEmail", { email: email })
        .then((response) => {
          //    
          if (response.data) {
            this.isEmailDuplicate = true;
            this.emailDuplicateCheckMessage = "   .";
          } else {
            this.isEmailDuplicate = false;
            this.emailDuplicateCheckMessage = "  .";
          }
        })
        .catch((error) => {
          console.error("     :", error);
          this.emailDuplicateCheckMessage = "    .";
        });
    },

    //  
    requestVerificationCode() {
      //   11 
      if (this.userForm.hp.length !== 11) {
        this.hpError = "* 11 .*";
        return; //   
      }

      //      
      axios
        .post("/api/checkHp", { hp: this.userForm.hp })
        .then((response) => {
          //     
          if (response.data >= 1) {
            //         
            this.hpError = "    .";
          } else {
            //       
            this.sendVerificationCode();
          }
        })
        .catch((error) => {
          console.error("      :", error);
        });
    },

    //      
    sendVerificationCode() {
      //     
      this.hpError = null; //   
      this.verificationRequested = true; //   
      this.verificationCode = ""; //    
      this.codeVerificationMessage = ""; //    
      this.messageClass = ""; //   

      //    
      clearTimeout(this.verificationCodeTimeout);
      //    
      axios
        .post("/api/sendSMS", this.userForm)
        .then((response) => {
          console.log(response.data);
          this.correctVerificationCode = response.data; //     
          this.setVerificationCodeTimeout(); //    
        })
        .catch((error) => {
          console.error("    :", error);
        });
    },

    //     
    setVerificationCodeTimeout() {
      this.verificationCodeTimeout = setTimeout(() => {
        alert(" .");
        this.correctVerificationCode = ""; //  
        if (this.verificationCode) {
          //    
          this.codeVerificationMessage =
            " .  .";
          this.messageClass = "error-message";
        }
      }, 10000); // 10  
    },
    verifyCodeInput() {
      if (!this.correctVerificationCode) {
        //  
        this.codeVerificationMessage =
          " .  .";
        this.messageClass = "error-message";
        return; //  
      }
      //            
      if (this.verificationCode && this.correctVerificationCode) {
        console.log("   : ", this.verificationCode);
        console.log("  : ", this.correctVerificationCode);

        const inputCode = String(this.verificationCode).trim();
        const correctCode = String(this.correctVerificationCode).trim();

        if (inputCode.length !== 6) {
          //    6    
          this.codeVerificationMessage = "  6 .";
          this.messageClass = "error-message";
        } else if (inputCode !== correctCode) {
          //       
          this.codeVerificationMessage = "   .";
          this.messageClass = "error-message";
        } else {
          //      
          this.codeVerificationMessage = "  .";
          this.messageClass = "success-message";
        }
      } else {
        //            
        this.codeVerificationMessage = "";
        this.messageClass = "";
      }
    },

    registerUser() {
      //   
      if (this.isUserIdDuplicate) {
        alert("   .   .");
        return; //      
      }
      if (this.codeVerificationMessage != "  .") {
        alert("  .");
        return; //   
      }

      //  
      axios
        .post("/api/register", this.userForm)
        .then((response) => {
          console.log(response.data);
          //       
          this.$router.push("/");
          this.hideModal();
        })
        .catch((error) => {
          console.error(" :", error.response);
        });
    },
  },
};
</script>

