  padding: 0.5rem; /*  */
  background-color: #f8f9fa; /*   */
  border-radius: 10px; /*   */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /*   */
  display: inline-block; /*     */
  width: auto; /*   */
  max-width: 100%; /*    */
}

