  position: fixed; /*    */
  left: 0;
  top: 0;
  width: 100%; /*   */
  height: 100%; /*   */
  background-color: rgba(0, 0, 0, 0.5); /*   */
  display: flex;
  justify-content: center; /*   */
  align-items: center; /*   */
  z-index: 1000; /*     */
}

.modal-content {
  background-color: #fff; /*  */
  padding: 20px; /*  */
  border-radius: 10px; /*   */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /*   */
  width: 500px; /*  */
  max-width: 80%; /*   */
  animation: modalOpen 0.5s ease; /*   */
}

@keyframes modalOpen {
  from {
    opacity: 0; /*   */
    transform: translateY(-50px); /*     */
  }
  to {
    opacity: 1; /*   */
    transform: translateY(0); /*   */
  }
}

.close {
  float: right; /*    */
  font-size: 25px; /*  */
  font-weight: bold;
  cursor: pointer; /*      */
  margin-left: 90%;
}

