<template>
  <div class="modal" v-if="isVisible">
    <div class="modal-content">
      <span class="close" @click="hideModal">&times;</span>
      <h2>/ </h2>
      <div class="modal-body" v-if="!isUserInfoVisible">
        <p>
             ?   
          .
        </p>
        <!-- ' '   -->
        <input
          type="text"
          v-model="phoneNumber"
          placeholder="  ('-' )"
          class="modal-input"
          required
          maxlength="11"
        />
        <button @click.prevent="requestVerificationCode">
          {{ verificationRequested ? "" : "  " }}
        </button>
        <p v-if="hpError" class="error-message">{{ hpError }}</p>
      </div>

      <div
        class="form-group"
        v-if="verificationRequested && !isUserInfoVisible"
      >
        <label for="verificationCode"> </label>
        <input
          type="text"
          id="verificationCode"
          name="verificationCode"
          v-model="verificationCode"
          required
          @input="verifyCodeInput"
          maxlength="6"
        />
        <p v-if="codeVerificationMessage" :class="messageClass">
          {{ codeVerificationMessage }}
        </p>
      </div>

      <div class="user-info" v-if="isUserInfoVisible">
        <h3> </h3>
        <p>: {{ userInfo.loginID }}</p>
        <p> : {{ userInfo.password }}</p>
        <p>*   .*</p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      isVisible: false, //   
      phoneNumber: "", //    
      verificationRequested: false, //    
      verificationCode: "", //    
      codeVerificationMessage: "", //     
      messageClass: "", //  CSS  (/)
      hpError: "", //     
      correctVerificationCode: "", //     
      isUserInfoVisible: false, //    
      userInfo: {}, //   
    };
  },
  methods: {
    showModal() {
      this.isVisible = true;
    },
    hideModal() {
      this.phoneNumber = "";
      this.verificationRequested = false;
      this.verificationCode = "";
      this.codeVerificationMessage = "";
      this.messageClass = "";
      this.hpError = "";
      this.correctVerificationCode = "";
      this.isUserInfoVisible = false;
      this.userInfo = {};
      this.isVisible = false;
      //  
      this.phoneNumber = "";
      this.verificationRequested = false;
      this.verificationCode = "";
      this.codeVerificationMessage = "";
      this.messageClass = "";
      this.hpError = "";
      this.correctVerificationCode = "";
    },

    requestVerificationCode() {
      //    
      if (!this.phoneNumber || this.phoneNumber.length !== 11) {
        this.hpError = "   .";
        return;
      }

      //  API        
      axios
        .post("/api/checkHp", { hp: this.phoneNumber })
        .then((response) => {
          //       
          if (response.data >= 1) {
            //     (    )
            this.sendVerificationCode();
          } else {
            //       
            this.hpError = "  .";
            this.verificationRequested = false; //    
          }
        })
        .catch((error) => {
          console.error("    : ", error);
          this.hpError = "    .";
        });
    },
    // 
    sendVerificationCode() {
      axios
        .post("/api/sendSMS", { hp: this.phoneNumber })
        .then((response) => {
          this.verificationRequested = true;
          console.log("  : ", response.data);
          this.correctVerificationCode = response.data;
          this.hpError = "";
        })
        .catch((error) => {
          console.error("     : ", error);
          this.hpError = "     .";
        });
    },
    verifyCodeInput() {
      console.log("  ", this.correctVerificationCode);
      console.log("  ", this.verificationCode);

      const inputCode = String(this.verificationCode).trim();
      const correctCode = String(this.correctVerificationCode).trim();
      //     (   )
      if (inputCode === correctCode) {
        //    ,      
        this.fetchUserInfo();
      } else {
        this.codeVerificationMessage = "  .";
        this.messageClass = "error-message";
      }
    },

    fetchUserInfo() {
      //  API    
      axios
        .post("/api/findAccount", { hp: this.phoneNumber })
        .then((response) => {
          console.log("response", response);
          console.log("response.data.loginID", response.data.loginID);
          //    
          // : response.data         
          this.displayUserInfo(response.data);
        })
        .catch((error) => {
          console.error("     : ", error);
          this.codeVerificationMessage =
            "     .";
          this.messageClass = "error-message";
        });
    },

    //  
    displayUserInfo(userInfo) {
      this.userInfo = userInfo; //   
      this.isUserInfoVisible = true; //   
      this.verificationRequested = false; //    
    },
  },
};
</script>

